export const topNavFunction = () => {
  var x = document.getElementById("myTopnav");
  if (x.className === "topnav") {
    x.className += " responsive";
  } else {
    x.className = "topnav";
  }
  var mn = document.getElementById("mobilemenu");
  if (mn.className === "mobilenav") {
    mn.className += " transition";
  } else {
    mn.className = "mobilenav";
  }
};
